@import url(https://fonts.googleapis.com/css?family=Blinker&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  padding-bottom: 20px;
  background: -webkit-gradient(linear, left top, left bottom, from(#11111C), to(#13131e));
  background: -webkit-linear-gradient(top, #11111C, #13131e);
  background: linear-gradient(to bottom, #11111C, #13131e);
  color: #E6E6E6;
  font-family: 'Blinker', sans-serif;
  cursor: default; }

p {
  width: 80%;
  margin-left: auto;
  margin-right: auto;
  font-size: 18px; }

.App {
  text-align: center; }

.welcome_section_inner {
  font-size: 18px; }

.cards_container {
  margin: 0 auto;
  width: 70%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap; }

.card {
  position: relative;
  padding: 0;
  margin: 15px;
  max-width: 600px;
  height: 277px;
  border-radius: 4px;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  -ms-border-radius: 4px;
  -o-border-radius: 4px;
  font-size: 20px;
  background-color: #31081F; }
  .card .card_image {
    height: 277px;
    transition: opacity .2s linear;
    -webkit-transition: opacity .2s linear;
    -moz-transition: opacity .2s linear;
    -ms-transition: opacity .2s linear;
    -o-transition: opacity .2s linear; }
  .card:hover .card_image {
    opacity: .15; }
  .card img {
    object-fit: cover;
    width: 100%;
    height: 277px;
    display: block;
    border-radius: 4px;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    -ms-border-radius: 4px;
    -o-border-radius: 4px; }

.links {
  position: absolute;
  bottom: 0;
  left: 0;
  margin: 5px;
  display: flex;
  list-style: none;
  -webkit-padding-start: 0;
          padding-inline-start: 0; }
  .links li {
    margin: 5px; }

.card {
  position: relative; }
  .card:hover .hover_info {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    display: block;
    flex-direction: column; }
  .card a {
    color: #C69BA3;
    font-size: 27px;
    display: inline-block;
    -webkit-transition: -webkit-transform .15s ease-out;
    transition: -webkit-transform .15s ease-out;
    transition: transform .15s ease-out;
    transition: transform .15s ease-out, -webkit-transform .15s ease-out;
    -webkit-transition: transform .15s ease-out;
    -moz-transition: transform .15s ease-out;
    -ms-transition: transform .15s ease-out;
    -o-transition: transform .15s ease-out; }
    .card a:visited {
      color: #C69BA3; }
    .card a:hover {
      color: #E6E6E6;
      transform: translateY(-3px);
      -webkit-transform: translateY(-3px);
      -moz-transform: translateY(-3px);
      -ms-transform: translateY(-3px);
      -o-transform: translateY(-3px); }

.hover_info {
  height: 277px;
  display: none; }

.hover_info--inner {
  height: 277px;
  margin: auto;
  width: 60%;
  height: 100%; }

.section_divider {
  margin: 40px 0;
  display: block;
  height: 2px;
  background-color: #A80D67; }

#project_hub {
  padding-top: 20px; }

#projects {
  margin-bottom: 0; }

.icons {
  margin: 10px auto;
  width: 50%; }
  .icons img {
    margin: 12px;
    height: 42px; }

h1, h2, h3 {
  font-weight: 400; }

.sticker_header {
  z-index: 1000;
  height: 40px;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background-color: #A80D67; }

.nav_header {
  display: flex;
  justify-content: flex-end; }
  .nav_header a {
    margin: 9px 10px; }

.welcome_section_wrapper {
  padding-top: 40px; }

a {
  line-height: 1;
  text-decoration: none;
  color: #C69BA3;
  font-size: 20px;
  display: inline-block; }
  a:visited {
    color: #C69BA3; }
  a:hover {
    color: #E6E6E6; }

#cv_button {
  padding: 10px;
  cursor: pointer;
  background-color: #11111C;
  color: #E6E6E6;
  font-size: 18px;
  border: 2px solid #A80D67;
  border-radius: 4px; }
  #cv_button:hover {
    background-color: #31081F; }

@media screen and (max-width: 767px) {
  p {
    font-size: 16px; }
  .nav_header {
    justify-content: center; }
  .hover_info--inner {
    width: 100%; }
  .icons {
    width: 90%; } }

@media screen and (max-width: 1023) {
  p {
    font-size: 16px; } }

